
ul {
  list-style: none;
  padding: 0 10px;
  position: relative;
  margin: 0;
}
@media screen and (max-width: 480px) {
.bookStorePhoto {
    margin: 10px 0;
    width: 100%;
    height: 200px;
    overflow: hidden;
    position: relative;
}
.bookStorePhoto img {
      width: 100%;
      position: absolute;
      top: -15%;
}
}
.namePosition {
  color: white;
  position: absolute;
  top: 30%;
  left: 50%;
  font-size: 1.5rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  letter-spacing: 0.3rem;
}



