
.mainTitle {
  color: white;
  margin: 0;
  font-weight: 700;
}
@media screen and (max-width: 480px) {
.mainTitle {
      font-size: 1.6rem;
      line-height: 50px;
}
}
@media screen and (max-width: 480px) {
.mapIcon {
    width: 50px;
    height: 50px;
}
}
.header {
  background: #ffba41;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media screen and (max-width: 480px) {
.header {
      padding: 0 10px;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
}



