
#map {
  height: 200px;
  width: 100%;
}
.titleColor {
  color: #ffba41;
}
.bestBookList {
  color: #3fa4f6;
}
.thumbnailArea {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
}
.thumbnailArea h2 {
    font-weight: 200;
    margin-top: 0.8rem;
    margin-bottom: 0;
}
.thumbnailArea p {
    margin-top: 0.2rem;
    margin-bottom: 15px;
    text-align: center;
    color: #afaaaa;
}
@media screen and (max-width: 480px) {
.thumbnailArea {
      width: 95%;
      margin: 10px auto;
}
.thumbnailArea h2 {
        font-size: 1.3rem;
}
.thumbnailArea p {
        width: 170px;
        font-size: 0.8rem;
}
.thumbnailArea .imageSize {
        width: 190px;
        height: 190px;
        border-radius: 100%;
        margin-top: 15px;
        overflow: hidden;
}
.thumbnailArea .imageSize img {
          width: 300px;
}
}
.whiteBox {
  background-color: white;
}
.whiteBox h3 {
    margin: 0;
    margin-bottom: 10px;
}
.whiteBox p {
    color: #afaaaa;
}
@media screen and (max-width: 480px) {
.whiteBox {
      width: 90%;
      margin: 10px auto;
      padding: 10px;
}
.whiteBox h3 {
        font-weight: 100;
}
.whiteBox p {
        width: 95%;
        margin: 0 auto;
}
}
.storeThumbnail {
  display: inline-block;
  width: 75px;
  height: 75px;
  overflow: hidden;
}
.storeThumbnail img {
    height: 100px;
}



